import React from 'react'
import Helmet from 'react-helmet'
import Layout from './layout'
import Photography from '../components/Photography'
import Miscellaneous from './Miscellaneous'
import me from '../assets/images/main/me.png'

class App extends React.Component {
  state = {}

  ChevronLink = ( toggle, text ) =>
    <span
      className={`link ${this.state[toggle] ? 'chevron-below' : ''}`}
      onClick={() => this.setState({
        showPhotography: false,
        showMiscellaneous: false,
        [toggle]: !this.state[toggle],
      })}>
    {text}
  </span>

  render() {
    const { showMiscellaneous, showPhotography } = this.state

    return (
      <Layout>
        <div>
          <Helmet>
            <meta charSet="utf-8"/>
            <title>Vikas Patidar, Computer Scientist</title>
          </Helmet>

          <section id="aboutMe" className="main style1">
            <div className="grid-wrapper">
              <div className="col-6">
                <header className="major">
                  <h2>About Me</h2>
                </header>
                <p>Hi, it's me. I live in New York city and a student of Courant School of Mathematical Sciences
                  New York University.</p>
                <p>I'm a machine learning enthusiastic.</p>
                <div className='about-me-links'>
                  <a href='#' target='_new'>Tech Blog</a>
                  {this.ChevronLink('showPhotography', 'Photography')}
                  {this.ChevronLink('showMiscellaneous', 'Etc')}
                </div>
              </div>
              <div className="col-6">
                <span className="image fit"><img src={me} alt="Vikas near New York City, USA"/></span>
              </div>
            </div>
          </section>
          {showPhotography && <Photography/>}
          {showMiscellaneous && <Miscellaneous/>}
        </div>
      </Layout>
    )
  }
}

export default App
