import React from 'react'

class Header extends React.Component {
  render() {
    return (
      <section id="header">
        <div className="inner">
          <h1 className='header'>I'M VIKAS.</h1>
          <div style={{ display: 'flex' }}>
            <p style={{ width: '25%' }}/>
            <p style={{ flex: 1 }}>I'm a student of Courant School of Mathematical Sciences,
              New York University.</p>
            <p style={{ width: '25%' }}/>

          </div>
        </div>
      </section>
    )
  }
}

export default Header
